//  []   
@Composable
fun VerticalVerdictTabs(
    selectedTab: String,
    onTabSelected: (String) -> Unit
) {
    Div({
        style {
            display(DisplayStyle.Flex)
            flexDirection(FlexDirection.Column)
            width(120.px)
            property("border-right", "1px solid ${Color("#e5e7eb")}")
            paddingRight(10.px)
            marginRight(15.px)
            property("flex-shrink", "0")
        }
    }) {
        allVerdictTabs().forEach { (key, label) ->
            val isSelected = key == selectedTab

            Button({
                attr("type", "button")
                onMouseDown { ev -> ev.preventDefault() }
                onClick { ev ->
                    ev.preventDefault()
                    ev.stopPropagation()
                    onTabSelected(key)
                }
                style {
                    padding(6.px, 10.px)
                    borderRadius(4.px)
                    border(0.px)
                    cursor("pointer")
                    fontSize(14.px)
                    property("text-align", "left")

                    backgroundColor(if (isSelected) Color("#e0f2fe") else Color.transparent)
                    color(if (isSelected) Color("#0369a1") else Color("#6b7280"))
                    property("font-weight", if (isSelected) "bold" else "normal")

                    marginTop(4.px)
                    marginBottom(4.px)
                }
            }) {
                Text(label)
            }
        }
    }
}

