@Composable
fun SourceListTab(entries: List<FoodSafetyEntry>) {
    Div({
        style {
            property("flex-grow", "1")
        }
    }) {
        Ul({
            style {
                listStyle("none")
                padding(0.px)
                margin(0.px)
            }
        }) {
            if (entries.isEmpty()) {
                P({ style { color(Color("#6b7280")) } }) { Text("    .") }
            } else {
                entries.forEach { entry -> SourceLine(entry) }
