}

@Composable
fun SourceLine(entry: FoodSafetyEntry) {
    Li({
        style {
            lineHeight("1.6")
            marginBottom(4.px)
            display(DisplayStyle.Flex)
            alignItems(AlignItems.Center)
            width(100.percent)
            gap(8.px)
        }
    }) {
        // 1) verdict 
        Span({
            style {
                backgroundColor(verdictBgRgba(entry.verdict, 0.15f))
                color(verdictColor(entry.verdict))
                padding(2.px, 6.px)
                borderRadius(6.px)
                fontSize(12.px)
                property("flex-shrink", "0")
                property("white-space", "nowrap")
            }
            title(verdictLabel(entry.verdict))
        }) { Text(verdictLabel(entry.verdict)) }

        // 2)  (/)  
        val href = entry.url?.takeIf { it.isNotBlank() }

        val textStyles: StyleScope.() -> Unit = {
            color(Color("#60a5fa"))
            property("flex-grow", "1")
            property("flex-shrink", "1")
            property("min-width", "0")
            property("overflow", "hidden")
            property("white-space", "nowrap")
            property("text-overflow", "ellipsis")
        }

        if (href != null) {
            A(href = href, attrs = {
                attr("target", "_blank")
                title(" ")
                style {
                    textDecoration("none")
                    color(Color("#2563eb"))
                    cursor("pointer")
                    flexGrow(1)
                    flexShrink(1)
                    property("min-width", "0")
                    property("overflow", "hidden")
                    property("white-space", "nowrap")
                    property("text-overflow", "ellipsis")
                }
                onMouseOver {
                    val el = it.target as HTMLElement
                    el.style.textDecoration = "underline"
                    el.style.color = "#2563eb"
                }
                onMouseOut {
                    val el = it.target as HTMLElement
                    el.style.textDecoration = "none"
                    el.style.color = "#2563eb"
                }
            }) {
                Div({
                    style {
                        display(DisplayStyle.Flex)
                        alignItems(AlignItems.Center)
                        gap(4.px)
                        property("min-width", "0")
                    }
                }) {
                    B({
                        style {
                            fontSize(14.px)
                            property("min-width", "0")
                            property("overflow", "hidden")
                            property("white-space", "nowrap")
                            property("text-overflow", "ellipsis")
                        }
                    }) { Text(entry.source) }

                    Span({
                        style {
                            fontSize(12.px)
                            color(Color("#2563eb"))
                        }
                    }) { Text("") }
                }
            }
        } else {
            B({ style(textStyles) }) { Text(entry.source) }
        }

        // 3)  
        Span({
            style {
                color(Color("#6b7280"))
                fontSize(12.px)
                property("flex-shrink", "0")
                property("white-space", "nowrap")
                marginRight(4.px)
            }
            title("  ")
        }) { Text(" ") }

        ReliabilityBar(entry.reliability)
    }
}

private data class Signal(val icon: String, val label: String, val bg: String, val fg: String)

//       (conditional )
private fun decideSignal(
    safePct: Double,
    conditionalPct: Double,
    cautionPct: Double,
    avoidPct: Double
): Signal {
    return when {
        avoidPct >= 40.0 ->
            Signal("", "  ", "#fee2e2", "#b91c1c")   // 
        cautionPct >= 40.0 ->
            Signal("", " ", "#fef3c7", "#92400e")   // 
        conditionalPct >= 40.0 ->
            Signal("", "  ", "#e0f2fe", "#075985") //  
        else ->
            Signal("", " ", "#dcfce7", "#166534")    // 
    }
}

//  
private fun isHigh(e: FoodSafetyEntry) = e.reliability >= 3.0
private fun isMid(e: FoodSafetyEntry)  = e.reliability >= 2.0 && e.reliability < 3.0
private fun isLow(e: FoodSafetyEntry)  = e.reliability >= 1.0 && e.reliability < 2.0

