@Composable
private fun GroupRecommendationCell(reliability: String, title: String, entries: List<FoodSafetyEntry>) {
    val total = entries.size
    if (total == 0) {
        Div({
            style {
                display(DisplayStyle.Flex)
                flexDirection(FlexDirection.Column)
                alignItems(AlignItems.Center)
                gap(6.px)
                padding(14.px)
                borderRadius(12.px)
                property("border", "1px solid #e5e7eb")
                backgroundColor(Color("#f9fafb"))
                textAlign("center")
                minHeight(110.px)
            }
        }) {
            Span({
                style {
                    color(Color("#166534"))
                    fontSize(12.px)
                    property("font-weight", "bold")
                    marginBottom(4.px)
                }
            }) { Text(reliability) }

            Span({
                style {
                    color(Color("#111827"))
                    fontSize(13.px)
                    fontWeight("600")
                    marginBottom(6.px)
                }
            }) { Text(title) }

            Div({
                style {
                    width(44.px); height(44.px)
                    borderRadius(999.px)
                    display(DisplayStyle.Flex)
                    alignItems(AlignItems.Center)
                    justifyContent(JustifyContent.Center)
                    fontSize(22.px)
                    color(Color("#9ca3af"))
                    backgroundColor(Color.white)
                    property("box-shadow", "0 1px 2px rgba(0,0,0,.05)")
                }
            }) { Text("") }
            Span({ style { color(Color("#9ca3af")); fontSize(12.px) } }) { Text(" ") }
        }
        return
    }

    val safe = entries.count { it.verdict.equals("safe", true) }
    val conditional = entries.count { it.verdict.equals("conditional", true) }
    val caution = entries.count { it.verdict.equals("caution", true) }
    val avoid = entries.count { it.verdict.equals("avoid", true) }

    fun pct(n: Int) = n * 100.0 / total

    //  conditional   
    val signal = decideSignal(
        safePct = pct(safe),
        conditionalPct = pct(conditional),
        cautionPct = pct(caution),
        avoidPct = pct(avoid)
    )

    Div({
        style {
            display(DisplayStyle.Flex)
            flexDirection(FlexDirection.Column)
            alignItems(AlignItems.Center)
            gap(6.px)
            padding(14.px)
            borderRadius(12.px)
            property("border", "1px solid #e5e7eb")
            backgroundColor(Color(signal.bg))
            textAlign("center")
            minHeight(110.px)
        }
    }) {
        //  
        Span({
            style {
                color(
                    when (reliability) {
                        " " -> Color("#166534")
                        " " -> Color("#92400e")
                        else -> Color("#991b1b")
                    }
                )
                fontSize(12.px)
                property("font-weight", "bold")
                marginBottom(2.px)
            }
        }) { Text(reliability) }

        //  
        Span({
            style { color(Color("#111827")); fontWeight("600"); fontSize(13.px) }
        }) { Text(title) }

        //   
        Div({
            style {
                width(48.px); height(48.px)
                borderRadius(999.px)
                display(DisplayStyle.Flex)
                alignItems(AlignItems.Center)
                justifyContent(JustifyContent.Center)
                fontSize(24.px)
                property("background-color", "white")
                property("box-shadow", "0 1px 2px rgba(0,0,0,.06)")
                property("border", "1px solid rgba(0,0,0,.04)")
                color(Color(signal.fg))
                marginTop(2.px)
            }
            title(signal.label)
        }) { Text(signal.icon) }

        //  
        Span({ style { color(Color(signal.fg)); fontSize(13.px) } }) {
            Text(signal.label)
        }

        //  
        Span({ style { color(Color("#4b5563")); fontSize(12.px) } }) {
            Text(" $safe   $conditional   $caution   $avoid")
        }
    }
}

//  3 
@Composable
fun TriGroupRecommendationRow(entries: List<FoodSafetyEntry>) {
    if (entries.isEmpty()) return

    val high = entries.filter(::isHigh)
    val mid  = entries.filter(::isMid)
    val low  = entries.filter(::isLow)

    Div({
        style {
            display(DisplayStyle.Grid)
            property("grid-template-columns", "repeat(3, minmax(0, 1fr))")
            gap(12.px)
            marginTop(10.px)
        }
    }) {
        GroupRecommendationCell(" ", " / ", high)
        GroupRecommendationCell(" ", " / ", mid)
        GroupRecommendationCell(" ", " /  / ", low)
    }
}


// === Reliability-aware summary ===
private enum class Tier { HIGH, MID, LOW }

private data class VerdictCounts(
    val safe: Int,
    val conditional: Int,
    val caution: Int,
    val avoid: Int
) {
    val total get() = safe + conditional + caution + avoid
    fun pct(n: Int) = if (total == 0) 0.0 else n * 100.0 / total
}

private fun tierOf(rel: Double): Tier = when {
    rel >= 3.0 -> Tier.HIGH
    rel >= 2.0 -> Tier.MID
    else       -> Tier.LOW
}

//  conditional   
private enum class Dominance { SAFE, CONDITIONAL, CAUTION, BAN, MIXED }

private fun dominanceOf(c: VerdictCounts): Dominance {
    if (c.total == 0) return Dominance.MIXED

    val safePct = c.pct(c.safe)
    val condPct = c.pct(c.conditional)
    val cautionPct = c.pct(c.caution)
    val avoidPct = c.pct(c.avoid)

    return when {
        avoidPct >= 50.0 -> Dominance.BAN
        cautionPct >= 40.0 || (avoidPct >= 30.0 && (cautionPct + avoidPct) >= 50.0) -> Dominance.CAUTION
        condPct >= 40.0 -> Dominance.CONDITIONAL
        safePct >= 60.0 -> Dominance.SAFE
        else -> Dominance.MIXED
    }
}

// entries   "     "     
fun buildReliabilityAwareSummary(entries: List<FoodSafetyEntry>): String {
    if (entries.isEmpty()) return ""

    //   
    val countsByTier: Map<Tier, VerdictCounts> = Tier.values().associateWith { tier ->
        val list = entries.filter { tierOf(it.reliability) == tier }
        VerdictCounts(
            safe        = list.count { it.verdict.equals("safe", true) },
            conditional = list.count { it.verdict.equals("conditional", true) },
            caution     = list.count { it.verdict.equals("caution", true) },
            avoid       = list.count { it.verdict.equals("avoid", true) }
        )
    }

    //  (High  Mid  Low)
    val primary: Tier? = listOf(Tier.HIGH, Tier.MID, Tier.LOW).firstOrNull { countsByTier[it]!!.total > 0 }
    val pCounts = primary?.let { countsByTier[it] } ?: VerdictCounts(0, 0, 0, 0)

    fun tierLabel(t: Tier) = when (t) {
        Tier.HIGH -> "   "
        Tier.MID  -> "    "
        Tier.LOW  -> "   "
    }

    val dom = dominanceOf(pCounts)

    //  conditional      
    val head = when (dom) {
        Dominance.SAFE ->
            "${tierLabel(primary!!)}    ."
        Dominance.CONDITIONAL ->
            "${tierLabel(primary!!)}      ."
        Dominance.CAUTION ->
            "${tierLabel(primary!!)}    ."
        Dominance.BAN ->
            "${tierLabel(primary!!)}   ."
        Dominance.MIXED ->
            "${tierLabel(primary!!)}  ."
    }

    //   (  : 2  or 30% )
    val others = Tier.values().filter { it != primary }
    val otherTotal = others.sumOf { countsByTier[it]!!.total }.coerceAtLeast(1)
    val otherSafe  = others.sumOf { countsByTier[it]!!.safe }
    val otherCond  = others.sumOf { countsByTier[it]!!.conditional }
    val otherCau   = others.sumOf { countsByTier[it]!!.caution }
    val otherAvoid = others.sumOf { countsByTier[it]!!.avoid }

    fun notable(n: Int) = n >= 2 || (n * 100.0 / otherTotal) >= 30.0

    val tail = when (dom) {
        Dominance.BAN -> {
            when {
                notable(otherSafe) || notable(otherCond) ->
                    " ,   /   ."
                else -> ""
            }
        }
        Dominance.CAUTION -> {
            when {
                notable(otherSafe) ->
                    " ,    ."
                notable(otherCond) ->
                    " ,      ."
                else -> ""
            }
        }
        Dominance.CONDITIONAL -> {
            when {
                notable(otherCau) || notable(otherAvoid) ->
                    " ,     ."
                notable(otherSafe) ->
                    " ,    ."
                else -> ""
            }
        }
        Dominance.SAFE -> {
            when {
                notable(otherAvoid) || notable(otherCau) ->
                    " ,    ."
                notable(otherCond) ->
                    " ,    ."
                else -> ""
            }
        }
        Dominance.MIXED ->
            " ( ${pCounts.safe}   ${pCounts.conditional}   ${pCounts.caution}   ${pCounts.avoid})"
    }

    return head + tail
}

@Composable
fun ReliabilityBar(rel: Double) {
    val level = when {
        rel >= 3.0 -> 3
        rel >= 2.0 -> 2
        else       -> 1
    }
    val fillWidth = (level * 14)

    val fillColor = when (level) {
        3 -> "#16a34a"
        2 -> "#d97706"
        else -> "#dc2626"
    }

    Div({
        style {
            width(42.px); height(8.px)
            borderRadius(999.px)
            backgroundColor(Color("#e5e7eb"))
            position(Position.Relative)
            property("flex-shrink", "0")
        }
        title(" ${fmt1(rel)}")
    }) {
        Div({
            style {
                width(fillWidth.px); height(8.px)
                borderRadius(999.px)
                backgroundColor(Color(fillColor))
                position(Position.Absolute)
                left(0.px); top(0.px)
            }
        })
    }
}


